@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  display: block;
  text-decoration: none; }

img {
  display: block;
  max-width: 100%; }

label {
  cursor: pointer; }

/*移除瀏覽器卷軸*/
html {
  overflow: -moz-hidden-unscrollable;
  height: 100%; }

body::-webkit-scrollbar {
  display: none; }

body {
  -ms-overflow-style: none;
  height: 100%;
  width: calc(100vw + 18px);
  overflow: auto; }

.position-relative {
  position: relative; }

.d-flex {
  display: flex; }

.align-item-center {
  align-items: center; }

.d-none {
  display: none !important; }

.mb-0 {
  margin-bottom: 0; }

header {
  background-color: #424242;
  max-width: 1920px; }

.wrap {
  max-width: 1200px;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  color: #FFD366;
  padding: 60px 0;
  width: 700px;
  position: relative; }
  @media (max-width: 577px) {
    .wrap {
      max-width: 576px;
      flex-direction: column; }
      .wrap h1 {
        margin-bottom: 50px; }
      .wrap ul {
        margin-bottom: 50px; } }
  @media (max-width: 376px) {
    .wrap {
      max-width: 375px; } }
  .wrap label {
    display: block;
    font-size: 18px;
    margin-bottom: 10px; }
  .wrap .inputClass {
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 1.5px;
    padding: 5px 7px;
    margin-bottom: 20px;
    border: 2px solid #FFD366;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.18);
    color: #FFD366; }
  .wrap .inputClass:focus {
    outline: none;
    padding: 3px 5px;
    border: 4px solid #FFD366; }
  .wrap h2 {
    color: #424242; }

.record {
  background-color: #f5f5f5;
  min-height: calc(100vh - 392px); }

footer {
  background-color: #FFD366;
  text-align: center; }
  footer img {
    display: inline-block;
    width: 55px;
    height: 55px;
    padding: 18px;
    filter: contrast(10%); }

.recordTitle {
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  font-weight: bold;
  margin-bottom: 30px; }
  .recordTitle .delListBtn {
    display: inline-block;
    font-weight: normal;
    font-size: 12px;
    padding-top: 5px;
    letter-spacing: 1px;
    color: #424242; }
  .recordTitle .delListBtn:hover {
    color: #FFD366; }

.recordList li {
  max-width: 624px;
  margin: 0 auto;
  background-color: #fff;
  color: #4A4A4A;
  font-size: 20px;
  line-height: 23px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  margin-bottom: 20px;
  font-weight: bold;
  border-left: 7px solid #000; }
  .recordList li span {
    font-weight: bold;
    font-size: 12px;
    line-height: 24px;
    padding-right: 10px;
    vertical-align: top; }

@media (max-width: 769px) {
  .recordList li {
    max-width: 576px; } }

@media (max-width: 376px) {
  .recordList li {
    max-width: 375px;
    flex-direction: column;
    align-items: start; }
    .recordList li div {
      margin-top: 20px;
      padding-left: 30px; }
    .recordList li span {
      margin-top: 20px; } }

.result {
  width: 140px; }
  @media (max-width: 769px) {
    .result {
      flex-direction: column;
      margin-bottom: 50px; } }

.rank {
  position: absolute;
  right: -170px;
  top: 50px;
  font-size: 32px;
  line-height: 38px;
  font-weight: bold;
  letter-spacing: 1px;
  width: 140px; }
  @media (max-width: 769px) {
    .rank {
      text-align: center;
      right: 0;
      top: 150px;
      margin-bottom: 10px; } }

.calBtn {
  border: none;
  width: 120px;
  height: 120px;
  border-radius: 100px;
  background-color: #FFD366;
  border: 6px solid #FFD366;
  font-size: 24px;
  line-height: 25px;
  font-weight: bold;
  display: flex;
  transition: .5s;
  color: #424242;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.calBtn:hover {
  box-shadow: 0px 0px 30px 6px rgba(255, 196, 50, 0.64);
  transition: .5s; }

.calBtn:active {
  background-color: #DEA921; }

.calBtn:focus {
  outline: none; }

.resetBtn {
  position: absolute;
  padding: 7px 10px;
  border-radius: 100px;
  border: 4px solid #424242;
  right: -9px;
  bottom: 15px;
  transition: .5s; }

.resetBtn:hover {
  transform: scale(1.2);
  transition: .5s; }

.resultImg {
  border: none;
  width: 120px;
  height: 120px;
  border-radius: 100px;
  background-color: #424242;
  border: 6px solid #FFD366;
  font-size: 32px;
  line-height: 25px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
  .resultImg span {
    font-size: 14px;
    line-height: 0;
    padding-top: 16px; }

h1 a {
  background-image: url(../../images/BMICLogo.png);
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
  background-size: cover;
  padding: 0;
  width: 117px;
  height: 117px; }
